import React from "react";
import Stack from "@mui/material/Stack";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Container from "@mui/material/Container";
import Autocomplete from "@mui/material/Autocomplete";
import Checkbox from "@mui/material/Checkbox";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormGroup from "@mui/material/FormGroup";
import FormLabel from "@mui/material/FormLabel";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import moment from "moment";
import {
  CustomDialog,
  CustomInput,
  ImageInput,
  TopNav,
} from "../../components";
import { useNavigate } from "react-router-dom";
import makeStyles from "@mui/styles/makeStyles";
import { connect } from "react-redux";
import {
  getPendidikan,
  getAgama,
  getPekerjaan,
  getJenisIdentitas,
  getStatusNikah,
  register,
  getIdentitas,
  setIdentitas,
  resetIdentitas,
  getPropinsi,
  getKotamadya,
  getKelurahan,
  getKecamatan,
} from "../../redux/actions";
import { isFormValid, isSelectedFieldValid } from "./validation";
import Dialog from "@mui/material/Dialog";
import Paper from "@mui/material/Paper";
import IconButton from "@mui/material/IconButton";
import Grid from "@mui/material/Grid";
import Close from "@mui/icons-material/Close";
import { Typography } from "@mui/material";
import { PRI_500 } from "../../theme/colors";

const useStyles = makeStyles(() => ({
  root: {
    padding: "16px 0",
  },
  langContainer: {
    padding: "16px 0",
    overflowX: "hidden",
  },
  dialogImg: {
    height: "250px",
    width: "auto",
  },
  numberIcon: {
    width: "35px",
    height: "35px",
    fontSize: "16px",
    fontWeight: 700,
    borderRadius: "50%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    border: "2px solid " + PRI_500,
  },
}));

const registerStep = [
  {
    img: "",
    text: {
      idn: "Isi data diri online, tanpa menulis dikertas",
      eng: "Fill your personal info, without writing it on paper",
    },
  },
  {
    img: "",
    text: {
      idn: "Isi data pribadi anda",
      eng: "Fill your personal info",
    },
  },
  {
    img: "",
    text: {
      idn: "Pilih tanggal kedatangan",
      eng: "Choose visit date",
    },
  },
  {
    img: "",
    text: {
      idn: "Datang ke RSUD Cengkareng sesuai tanggal",
      eng: "Come to RSUD Cengkareng according to the date",
    },
  },
  {
    img: "",
    text: {
      idn: "Dapatkan kartu pasien atau nomer MR",
      eng: "Get patient's card or MR number",
    },
  },
  {
    img: "",
    text: {
      idn: "Selesai, anda sekarang bagian dari keluarga RSUD Cengkareng",
      eng: "Done, now you're part of RSUD Cengkareng's family",
    },
  },
];
const RegisterPage = (props) => {
  const {
    actGetPendidikan,
    stateBahasa,
    actGetAgama,
    actGetPekerjaan,
    actGetJenisID,
    actGetStatusNikah,
    stateAgama,
    statePendidikan,
    statePekerjaan,
    stateStatusNikah,
    stateJenisID,
    stateError,
    statePropinsi,
    stateKotamadya,
    stateKecamatan,
    stateKelurahan,
    actRegister,
    stateIdentitas,
    actGetIdentitas,
    actSetIdentitas,
    actResetIdentitas,
    actGetPropinsi,
    actGetKotamadya,
    actGetKecamatan,
    actGetKelurahan,
  } = props;

  const {
    jenisId,
    jenisIdnomor,
    namaLengkap,
    namaPanggilan,
    tempatLahir,
    tglLahir,
    jenisKelamin,
    alamat,
    rt,
    rw,
    kelurahan,
    kecamatan,
    kotaDati,
    kodePos,
    propinsi,
    noTelpon,
    agama,
    nilaiYakin,
    textNilaiYakin,
    statusNikah,
    statusWarganegara,
    infoIbu,
    infoAyah,
    infoIstriSuami,
    noPenjamin,
    namaWaliDarurat,
    telponWaliDarurat,
    fileUpload,
    filePhoto,
    noTelpon2,
    alamatDomisili,
    statusPendidikan,
    penjamin,
    bahasa,
    infoKerja,
    penjaminSubjenis,
  } = stateIdentitas;

  const invalidInitial = {
    jenisId: "",
    jenisIdnomor: "",
    namaLengkap: "",
    namaPanggilan: "",
    tempatLahir: "",
    tglLahir: "",
    jenisKelamin: "",
    alamat: "",
    rt: "",
    rw: "",
    kelurahan: "",
    kecamatan: "",
    kotaDati: "",
    kodePos: "",
    propinsi: "",
    noTelpon: "",
    agama: "",
    nilaiYakin: "",
    textNilaiYakin: "",
    statusNikah: "",
    statusWarganegara: "",
    infoIbu: "",
    infoAyah: "",
    infoIstriSuami: "",
    noPenjamin: "",
    namaWaliDarurat: "",
    telponWaliDarurat: "",
    fileUpload: "",
    filePhoto: "",
    // fligFlag: true,
    // inputWaktu: "",
    noTelpon2: "",
    alamatDomisili: "",
    statusPendidikan: "",
    penjamin: "",
    bahasa: "",
    infoKerja: "",
    penjaminSubjenis: 0,
    // id_pasien_ps_id: null,
  };

  const classes = useStyles();
  const [openFail, setOpenFail] = React.useState(false);
  const [stepDialog, setStepDialog] = React.useState(true);
  const [sameAddress, setHandleSameAddress] = React.useState(false);
  const [errorForm, setErrorForm] = React.useState("");
  const [succForm, setSuccForm] = React.useState(false);

  // const [form, setForm] = React.useState(formInitial);
  const [formInvalid, setInvalid] = React.useState(invalidInitial);

  const navigate = useNavigate();
  React.useEffect(() => {
    if (stateError) {
      navigate("/exception-handling");
    }
  }, [stateError]);

  React.useEffect(() => {
    actGetPekerjaan();
    actGetAgama();
    actGetPendidikan();
    actGetJenisID();
    actGetStatusNikah();
    actGetPropinsi();
  }, []);

  const changeForm = (elem) => {
    let { name, id, value } = elem.target;
    let { error } = isSelectedFieldValid(name ? name : id, value);
    setInvalid({
      ...formInvalid,
      [name ? name : id]: error ? "*" + error.message : "",
    });
    actSetIdentitas({
      key: name ? name : id,
      value: value,
    });
    // return setForm({
    //   ...form,
    //   [name ? name : id]: value,
    // });
    // return;
  };

  const changeFormAuto = (elem) => {
    let { name, id, value } = elem;
    let { error } = isSelectedFieldValid(name, value);
    switch (name) {
      case "propinsi":
        actGetKotamadya(id);
        break;
      case "kotaDati":
        actGetKecamatan(id);
        break;
      case "kecamatan":
        actGetKelurahan(id);
        break;
      case "kelurahan":
        break;
      default:
        break;
    }
    setInvalid({
      ...formInvalid,
      [name]: error ? "*" + error.message : "",
    });
    actSetIdentitas({
      key: name,
      value: value,
    });
  };
  const handleChangeImg = (elem) => {
    //  actSetIdentitas({
    //    key: "fileUpload",
    //    value: elem.target.value,
    //  });
    actSetIdentitas({
      key: "filePhoto",
      value: elem.target.value,
    });
  };
  const [submitted, setSubmitted] = React.useState(false);
  const handleSubmit = () => {
    let isValid = isFormValid(stateIdentitas);
    setSubmitted(true);

    if (!isValid.error) {
      return actRegister(stateIdentitas, {
        success: () => {
          actResetIdentitas();
          setSuccForm(true);
          setSubmitted(false);
        },
        failed: (msg) => {
          setErrorForm(msg);
          setSubmitted(false);
        },
      });
    } else {
      if (
        isValid.error.details[0].context &&
        isValid.error.details[0].context.key
      ) {
        let key = isValid.error.details[0].context.key;
        setInvalid({
          ...formInvalid,
          [key]: isValid.error.details[0].message,
        });
      }
      setErrorForm(isValid.error.details[0].message || "Mohon Lengkapi Form!.");
      setSubmitted(false);
    }
  };

  const handleGetNIK = () => {
    if (jenisId === "1" && jenisIdnomor.length === 16) {
      actGetIdentitas(jenisIdnomor);
    }
  };
  const handleSameAddress = () => {
    setHandleSameAddress(!sameAddress);
    actSetIdentitas({
      key: "alamatDomisili",
      value: alamat,
    });
  };
  return (
    <>
      <Dialog open={stepDialog} fullWidth onClose={() => setStepDialog(false)}>
        <Paper
          sx={{
            padding: "8px",
          }}
        >
          <Grid container spacing={2}>
            <Grid item container justifyContent="end" xs={12}>
              <IconButton onClick={() => setStepDialog(false)}>
                <Close />
              </IconButton>
            </Grid>
            {registerStep.map((item, index) => {
              return (
                <Grid
                  container
                  item
                  xs={12}
                  spacing={1}
                  alignItems="center"
                  key={index + "_regstep"}
                >
                  <Grid item xs={2}>
                    <div className={classes.numberIcon}>{index + 1}</div>
                  </Grid>
                  <Grid item xs={10}>
                    <Typography variant="body1">
                      {stateBahasa === "indonesia"
                        ? item.text.idn
                        : item.text.eng}
                    </Typography>
                  </Grid>
                </Grid>
              );
            })}
            <Grid item xs={12}>
              <Button variant="contained" onClick={() => setStepDialog(false)}>
                {stateBahasa === "indonesia" ? "Tutup" : "Close"}
              </Button>
            </Grid>
          </Grid>
        </Paper>
      </Dialog>

      <CustomDialog
        setOpen={setSuccForm}
        type={"success"}
        title={"Berhasil Terdaftar!."}
        open={succForm}
      />
      <CustomDialog
        setOpen={setErrorForm}
        type={"error"}
        title={errorForm}
        open={errorForm ? true : false}
      />
      <Container className={classes.root}>
        <Stack spacing={2}>
          <TopNav
            type="back"
            title={
              stateBahasa === "indonesia"
                ? "Daftar Pasien Baru"
                : "Register New Patient"
            }
            click={() => navigate(-1)}
          />
          <FormControl
            fullWidth
            required
            error={formInvalid.jenisId ? true : false}
          >
            <InputLabel id="jenisId">
              {stateBahasa === "indonesia"
                ? "Jenis Identitas"
                : "Identity Type"}
            </InputLabel>
            <Select
              labelId="jenisId"
              id="jenisId"
              name="jenisId"
              value={jenisId}
              onChange={changeForm}
            >
              {stateJenisID &&
                stateJenisID.map((item) => {
                  return (
                    <MenuItem
                      key={"jenisID" + item.ref_jki_id}
                      value={item.ref_jki_id.toString()}
                    >
                      {item.ref_jki_ket}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <TextField
            id="jenisIdnomor"
            name="jenisIdnomor"
            label={
              stateBahasa === "indonesia" ? "No Identitas" : "Identity Number"
            }
            required
            error={formInvalid.jenisIdnomor ? true : false}
            value={jenisIdnomor}
            onChange={changeForm}
            helperText={formInvalid.jenisIdnomor}
            onBlur={handleGetNIK}
          />
          <TextField
            id="namaLengkap"
            name="namaLengkap"
            label={stateBahasa === "indonesia" ? "Nama Lengkap" : "Fullname"}
            required
            value={namaLengkap}
            error={formInvalid.namaLengkap ? true : false}
            onChange={changeForm}
            helperText={formInvalid.namaLengkap}
          />
          <TextField
            id="namaPanggilan"
            name="namaPanggilan"
            label={stateBahasa === "indonesia" ? "Nama Panggilan" : "Nickname"}
            required
            value={namaPanggilan}
            onChange={changeForm}
            error={formInvalid.namaPanggilan ? true : false}
            helperText={formInvalid.namaPanggilan}
          />
          <TextField
            id="tempatLahir"
            name="tempatLahir"
            label={stateBahasa === "indonesia" ? "Tempat Lahir" : "Birthplace"}
            // label="Tempat Lahir - Birthplace"
            required
            value={tempatLahir}
            onChange={changeForm}
            error={formInvalid.tempatLahir ? true : false}
            helperText={formInvalid.tempatLahir}
          />
          <TextField
            id="tglLahir"
            name="tglLahir"
            label={stateBahasa === "indonesia" ? "Tanggal Lahir" : "Birthdate"}
            // label="Tanggal Lahir - Birthdate"
            required
            InputLabelProps={{
              shrink: true,
            }}
            inputProps={{
              max: moment().format("YYYY-MM-DD"),
            }}
            type={"date"}
            value={tglLahir}
            onChange={changeForm}
            error={formInvalid.tglLahir ? true : false}
            helperText={formInvalid.tglLahir}
          />
          <FormControl required error={formInvalid.jenisKelamin ? true : false}>
            <FormLabel id="jenisKelamin">
              {stateBahasa === "indonesia" ? "Jenis Kelamin" : "Gender"}
            </FormLabel>
            {/* <FormLabel id="jenisIdentitas">Jenis Kelamin - Gender</FormLabel> */}
            <RadioGroup
              row
              aria-labelledby="jeniKelamin"
              id="jenisKelamin"
              name="jenisKelamin"
              value={jenisKelamin}
              onChange={changeForm}
            >
              <FormControlLabel
                value="1"
                control={<Radio />}
                label={stateBahasa === "indonesia" ? "Pria" : "Male"}
                // label="Pria - Male"
              />
              <FormControlLabel
                value="2"
                control={<Radio />}
                label={stateBahasa === "indonesia" ? "Perempuan" : "Female"}

                // label="Perempuan - Female"
              />
            </RadioGroup>
          </FormControl>
          <Autocomplete
            id="propinsi"
            name="propinsi"
            freeSolo
            // onChange={(el) => console.log(el.target)}
            value={propinsi}
            onChange={(el, val) =>
              changeFormAuto({
                name: "propinsi",
                value: val.label,
                id: val.id,
              })
            }
            inputValue={propinsi}
            onInputChange={(event, newInputValue) =>
              changeForm({
                target: {
                  name: "propinsi",
                  value: newInputValue,
                },
              })
            }
            options={statePropinsi.map((option) => {
              return {
                id: option.ref_propinsi_id,
                label: option.ref_propinsi_nama,
              };
            })}
            renderInput={(params) => (
              <TextField
                {...params}
                fullWidth
                label={stateBahasa === "indonesia" ? "Provinsi" : "Province"}
                required
                helperText={formInvalid.propinsi}
                error={formInvalid.propinsi ? true : false}
              />
            )}
          />
          <Autocomplete
            id="kotaDati"
            name="kotaDati"
            freeSolo
            inputValue={kotaDati}
            onInputChange={(event, newInputValue) => {
              changeForm({
                target: {
                  name: "kotaDati",
                  value: newInputValue,
                },
              });
            }}
            value={kotaDati}
            onChange={(el, val) =>
              changeFormAuto({
                name: "kotaDati",
                value: val.label,
                id: val.id,
              })
            }
            options={stateKotamadya.map((option) => {
              return {
                id: option.ref_kotadati2_id,
                label: option.ref_kotadati2_nama,
              };
            })}
            renderInput={(params) => (
              <TextField
                {...params}
                fullWidth
                label={
                  stateBahasa === "indonesia"
                    ? "Kotamadya / Dati II"
                    : "Regency"
                }
                required
                helperText={formInvalid.kotaDati}
                error={formInvalid.kotaDati ? true : false}
              />
            )}
          />
          <Autocomplete
            id="kecamatan"
            name="kecamatan"
            freeSolo
            inputValue={kecamatan}
            onInputChange={(event, newInputValue) => {
              changeForm({
                target: {
                  name: "kecamatan",
                  value: newInputValue,
                },
              });
            }}
            value={kecamatan}
            onChange={(el, val) =>
              changeFormAuto({
                name: "kecamatan",
                value: val.label,
                id: val.id,
              })
            }
            options={stateKecamatan.map((option) => {
              return {
                id: option.ref_kcamatan_id,
                label: option.ref_kcamatan_nama,
              };
            })}
            renderInput={(params) => (
              <TextField
                {...params}
                fullWidth
                label={stateBahasa === "indonesia" ? "Kecamatan" : "Country"}
                required
                helperText={formInvalid.kecamatan}
                error={formInvalid.kecamatan ? true : false}
              />
            )}
          />
          <Autocomplete
            id="kelurahan"
            name="kelurahan"
            freeSolo
            inputValue={kelurahan}
            onInputChange={(event, newInputValue) => {
              changeForm({
                target: {
                  name: "kelurahan",
                  value: newInputValue,
                },
              });
            }}
            value={kelurahan}
            onChange={(el, val) =>
              changeFormAuto({
                name: "kelurahan",
                value: val.label,
                id: val.id,
              })
            }
            options={stateKelurahan.map((option) => {
              return {
                id: option.ref_klurahan_id,
                label: option.ref_klurahan_nama,
              };
            })}
            renderInput={(params) => (
              <TextField
                {...params}
                fullWidth
                label={
                  stateBahasa === "indonesia" ? "Kelurahan" : "Subdistrict"
                }
                required
                helperText={formInvalid.kelurahan}
                error={formInvalid.kelurahan ? true : false}
              />
            )}
          />
          {/* <TextField
            fullWidth
            id="propinsi"
            name="propinsi"
            // label="Provinsi - Province"
            label={stateBahasa === "indonesia" ? "Provinsi" : "Province"}
            required
            value={propinsi}
            onChange={changeForm}
            helperText={formInvalid.propinsi}
            error={formInvalid.propinsi ? true : false}
          /> */}
          {/* <TextField
            fullWidth
            id="kotaDati"
            name="kotaDati"
            // label="Kotamadya / Dati II - Regency"
            label={
              stateBahasa === "indonesia" ? "Kotamadya / Dati II" : "Regency"
            }
            required
            value={kotaDati}
            onChange={changeForm}
            helperText={formInvalid.kotaDati}
            error={formInvalid.kotaDati ? true : false}
          />
          <TextField
            fullWidth
            id="kecamatan"
            // label="Kecamatan - Country"
            label={stateBahasa === "indonesia" ? "Kecamatan" : "Country"}
            required
            value={kecamatan}
            onChange={changeForm}
            helperText={formInvalid.kecamatan}
            error={formInvalid.kecamatan ? true : false}
          />
          <TextField
            fullWidth
            id="kelurahan"
            name="kelurahan"
            // label="Kelurahan - Subdistrict"
            label={stateBahasa === "indonesia" ? "Kelurahan" : "Subdistrict"}
            required
            value={kelurahan}
            onChange={changeForm}
            helperText={formInvalid.kelurahan}
            error={formInvalid.kelurahan ? true : false}
          /> */}
          <TextField
            fullWidth
            id="alamat"
            name="alamat"
            // label="Alamat Sesuai Identitas - Address ID"
            label={
              stateBahasa === "indonesia"
                ? "Alamat Sesuai Identitas"
                : "Address ID"
            }
            required
            value={alamat}
            onChange={changeForm}
            helperText={formInvalid.alamat}
            error={formInvalid.alamat ? true : false}
          />

          <Stack direction="row" spacing={2}>
            <TextField
              fullWidth
              id="rt"
              name="rt"
              label="RT"
              required
              value={rt}
              onChange={changeForm}
              helperText={formInvalid.rw}
              error={formInvalid.rt ? true : false}

              // value={type}
              // onChange={changeType}
            />
            <TextField
              fullWidth
              id="rw"
              name="rw"
              label="RW"
              required
              value={rw}
              onChange={changeForm}
              helperText={formInvalid.rw}
              error={formInvalid.rw ? true : false}
            />
          </Stack>

          <TextField
            fullWidth
            id="kodePos"
            name="kodePos"
            // label="Kode Pos - Post Code"
            label={stateBahasa === "indonesia" ? "Kode Pos" : "Post Code"}
            required
            value={kodePos}
            onChange={changeForm}
            helperText={formInvalid.kodePos}
            error={formInvalid.kodePos ? true : false}
          />
          <div>
            <TextField
              id="alamatDomisili"
              name="alamatDomisili"
              // label="Alamat Domisili - Current Address"
              label={
                stateBahasa === "indonesia"
                  ? "Alamat Domisili"
                  : "Current Address"
              }
              required
              fullWidth
              disabled={sameAddress}
              error={formInvalid.alamatDomisili ? true : false}
              value={alamatDomisili}
              onChange={changeForm}
              helperText={formInvalid.alamatDomisili}
            />
            <FormGroup>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={sameAddress}
                    onChange={handleSameAddress}
                  />
                }
                label={
                  stateBahasa === "indonesia"
                    ? "Sama dengan alamat domisili"
                    : "Same as current address"
                }
              />
            </FormGroup>
          </div>

          <Stack direction="row" spacing={2}>
            <CustomInput
              id="noTelpon"
              name="noTelpon"
              label={
                stateBahasa === "indonesia" ? "No Telp 1" : "Phone Number 1"
              }
              fullWidth
              required
              value={noTelpon}
              onChange={changeForm}
              helperText={formInvalid.noTelpon}
              className={classes.inputPhone}
              inputType="phone-number"
              error={formInvalid.noTelpon ? true : false}
            />
            {/* <TextField
              fullWidth
              id="noTelpon"
              name="noTelpon"
              // label="No Telp - Phone Number 1"
              label={stateBahasa === "indonesia" ? "No Telp 1" : "Phone Number 1"}
              required
              value={noTelpon}
              onChange={changeForm}
              helperText={formInvalid.noTelpon}
              // value={type}
              // onChange={changeType}
            /> */}
            <CustomInput
              id="noTelpon2"
              name="noTelpon2"
              label={
                stateBahasa === "indonesia" ? "No Telp 2" : "Phone Number 2"
              }
              fullWidth
              required
              className={classes.inputPhone}
              inputType="phone-number"
              value={noTelpon2}
              onChange={changeForm}
              helperText={formInvalid.noTelpon2}
            />
            {/* <TextField
              fullWidth
              id="noTelpon2"
              name="noTelpon2"
              fullWidth
              label={stateBahasa === "indonesia" ? "No Telp 2" : "Phone Number 2"}
              // label="No Telp - Phone Number 2"
              required
              value={noTelpon2}
              onChange={changeForm}
              helperText={formInvalid.noTelpon2}
            /> */}
          </Stack>
          <FormControl fullWidth required>
            {/* <InputLabel id="agama">Agama - Religion</InputLabel> */}
            <InputLabel id="agama">
              {stateBahasa === "indonesia" ? "Agama" : "Religion"}
            </InputLabel>
            <Select
              labelId="agama"
              id="agama"
              name="agama"
              value={agama}
              onChange={changeForm}

              //   value={age}
              //   label="Age"
              //   onChange={handleChange}
            >
              {stateAgama &&
                stateAgama.map((item) => {
                  return (
                    <MenuItem
                      key={"agama" + item.ref_agama_id}
                      value={item.ref_agama_id}
                    >
                      {item.ref_agama_ket}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <Stack spacing={1}>
            <FormControl required>
              {/* <FormLabel id="keyakinan">
              Keyakinan dan Nilai-Nilai Pribadi - Personal Believes and Values
            </FormLabel> */}
              <FormLabel id="keyakinan">
                {stateBahasa === "indonesia"
                  ? "Keyakinan dan Nilai-Nilai Pribadi"
                  : "Personal Believes and Values"}
              </FormLabel>
              <RadioGroup
                row
                aria-labelledby="keyakinan"
                name="nilaiYakin"
                id="nilaiYakin"
                value={nilaiYakin}
                onChange={changeForm}
              >
                <FormControlLabel
                  value={0}
                  control={<Radio />}
                  label={stateBahasa === "indonesia" ? "Tidak" : "No"}
                />
                <FormControlLabel
                  value={1}
                  control={<Radio />}
                  // label="Ya (Tidak mau imunisasi/Tidak mau transfusi/Tidak mau pulang sabtu/Vegetarian)"
                  label={
                    stateBahasa === "indonesia"
                      ? "Ya (Tidak mau imunisasi/Tidak mau transfusi/Tidak mau pulang sabtu/Vegetarian"
                      : "Yes (Won't immunization/Won't Transfusion/Won't go home on Saturday/Vegetarian)"
                  }
                />
              </RadioGroup>
            </FormControl>
            <TextField
              fullWidth
              id="textNilaiYakin"
              name="textNilaiYakin"
              disabled={nilaiYakin === "1" ? false : true}
              label={
                stateBahasa === "indonesia"
                  ? "Jika ya, sebutkan"
                  : "If yes, mention"
              }
              required
              value={textNilaiYakin}
              onChange={changeForm}
              helperText={formInvalid.textNilaiYakin}
            />
          </Stack>
          <FormControl fullWidth required>
            {/* <InputLabel id="agama">
              Status Pernikahan - Marital Status
            </InputLabel> */}
            <InputLabel id="agama">
              {stateBahasa === "indonesia"
                ? "Status Pernikahan"
                : "Marital Status"}
            </InputLabel>
            <Select
              labelId="statusNikah"
              id="statusNikah"
              name="statusNikah"
              value={statusNikah}
              onChange={changeForm}
            >
              {stateStatusNikah &&
                stateStatusNikah.map((item) => {
                  return (
                    <MenuItem
                      key={"jenisID" + item.stat_id}
                      value={item.stat_id}
                    >
                      {item.stat_ket}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <FormControl fullWidth required>
            {/* <InputLabel id="pendidikan">Pendidikan - Education</InputLabel> */}
            <InputLabel id="pendidikan">
              {stateBahasa === "indonesia" ? "Pendidikan" : "Education"}
            </InputLabel>
            <Select
              labelId="statusPendidikan"
              id="statusPendidikan"
              name="statusPendidikan"
              value={statusPendidikan}
              onChange={changeForm}
            >
              {statePendidikan &&
                statePendidikan.map((item) => {
                  return (
                    <MenuItem
                      key={"pnddkn" + item.pend_id}
                      value={item.pend_id}
                    >
                      {item.pend_nama}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <FormControl required>
            {/* <FormLabel id="kewarganegaraan">
              Kewarganegaraan - Nationality
            </FormLabel> */}
            <FormLabel id="kewarganegaraan">
              {stateBahasa === "indonesia" ? "Kewarganegaraan" : "Nationality"}
            </FormLabel>
            <RadioGroup
              row
              aria-labelledby="statusWarganegara"
              name="statusWarganegara"
              value={statusWarganegara}
              onChange={changeForm}
            >
              <FormControlLabel
                value="1"
                control={<Radio />}
                // label="WNI - Citizen"
                label={stateBahasa === "indonesia" ? "WNI" : "Citizen"}
              />
              <FormControlLabel
                value="2"
                control={<Radio />}
                // Foreigner
                label={stateBahasa === "indonesia" ? "WNA" : "Foreigner"}
                // label="WNA - Foreigner"
              />
            </RadioGroup>
          </FormControl>
          <TextField
            fullWidth
            id="bahasa"
            name="bahasa"
            label={stateBahasa === "indonesia" ? "Bahasa" : "Language"}
            // label="Bahasa - Language"
            required
            value={bahasa}
            onChange={changeForm}
            helperText={formInvalid.bahasa}
            error={formInvalid.bahasa ? true : false}
          />
          <FormControl fullWidth required>
            <InputLabel id="pekerjaan">
              {stateBahasa === "indonesia" ? "Pekerjaan" : "Occupation"}
            </InputLabel>
            {/* <InputLabel id="pekerjaan">Pekerjaan - Occupation</InputLabel> */}
            <Select
              labelId="infoKerja"
              id="infoKerja"
              name="infoKerja"
              value={infoKerja}
              onChange={changeForm}
            >
              {statePekerjaan &&
                statePekerjaan.map((item) => {
                  return (
                    <MenuItem
                      key={"pkrjn" + item.ref_pkrjn_id}
                      value={item.ref_pkrjn_id}
                    >
                      {item.ref_pkrjn_nama}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <TextField
            fullWidth
            id="infoAyah"
            name="infoAyah"
            // label="Nama Bapak - Father's Name"
            label={stateBahasa === "indonesia" ? "Nama Bapak" : "Father's Name"}
            required
            value={infoAyah}
            onChange={changeForm}
            helperText={formInvalid.infoAyah}
            error={formInvalid.infoAyah ? true : false}
          />
          <TextField
            fullWidth
            id="infoIstriSuami"
            name="infoIstriSuami"
            // label="Nama Suami / Istri - Husband's / Wife's Name"
            label={
              stateBahasa === "indonesia"
                ? "Nama Suami / Istri"
                : "Husband's / Wife's Name"
            }
            required
            value={infoIstriSuami}
            onChange={changeForm}
            helperText={formInvalid.infoIstriSuami}
            error={formInvalid.infoIstriSuami ? true : false}
          />
          <Stack spacing={1}>
            <FormControl fullWidth required>
              <InputLabel id="penjamin">
                {stateBahasa === "indonesia" ? "Penjamin" : "Insurance"}
              </InputLabel>
              {/* <InputLabel id="penjamin">Penjamin - Insurance</InputLabel> */}
              <Select
                labelId="penjamin"
                id="penjamin"
                name="penjamin"
                value={penjamin}
                onChange={changeForm}
              >
                <MenuItem value={1}>Umum - Public</MenuItem>
                <MenuItem value={2}>BPJS - BPJS</MenuItem>
                <MenuItem value={3}>Perusahaan - Company</MenuItem>
                <MenuItem value={4}>Asuransi - Insurance</MenuItem>
              </Select>
            </FormControl>
            <TextField
              fullWidth
              id="noPenjamin"
              name="noPenjamin"
              label={
                stateBahasa === "indonesia" ? "No Penjamin" : "Insurance No"
              }
              // label="No Penjamin - Insurance No"
              required
              value={noPenjamin}
              onChange={changeForm}
              helperText={formInvalid.noPenjamin}
            />
          </Stack>
          <Stack direction={"row"} spacing={2}>
            <TextField
              fullWidth
              id="namaWaliDarurat"
              name="namaWaliDarurat"
              label={
                stateBahasa === "indonesia" ? "Nama Wali" : "Guarantor's Name"
              }
              // label="Nama Wali - Guarantor's Name"
              required
              value={namaWaliDarurat}
              onChange={changeForm}
              helperText={formInvalid.namaWaliDarurat}
              error={formInvalid.namaWaliDarurat ? true : false}
            />
            <CustomInput
              id="telponWaliDarurat"
              name="telponWaliDarurat"
              fullWidth
              label={
                stateBahasa === "indonesia"
                  ? "Telp Penanggung"
                  : "Guarantor's Phone Number"
              }
              required
              className={classes.inputPhone}
              inputType="phone-number"
              value={telponWaliDarurat}
              onChange={changeForm}
              helperText={formInvalid.telponWaliDarurat}
              error={formInvalid.telponWaliDarurat ? true : false}
            />
            {/* <TextField
              fullWidth
              id="telponWaliDarurat"
              label={
                stateBahasa === "indonesia"
                  ? "Telp Penanggung"
                  : "Guarantor's Phone Number"
              }
              // label="Telp Penanggung - Guarantor's Phone Number"
              value={telponWaliDarurat}
              onChange={changeForm}
              helperText={formInvalid.telponWaliDarurat}
            /> */}
          </Stack>
          <ImageInput
            is_required={true}
            is_readonly={false}
            // field_name={"Foto Identitas - KTP - ID Photo"}
            field_name={
              stateBahasa === "indonesia" ? "Foto Identitas - KTP" : "ID Photo"
            }
            default_value={filePhoto}
            value={filePhoto}
            form_child_id={""}
            handleChange={handleChangeImg}
            is_error={false}
          />

          <Button
            variant="contained"
            onClick={handleSubmit}
            disabled={submitted}
            // onClick={() => navigate("/")}
          >
            {stateBahasa === "indonesia" ? "Daftar" : "Submit"}
          </Button>
        </Stack>
      </Container>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    stateIdentitas: state.auth.identitas,
    stateAgama: state.master.agama,
    statePendidikan: state.master.pendidikan,
    statePekerjaan: state.master.pekerjaan,
    stateJenisID: state.master.jenisIdentitas,
    stateStatusNikah: state.master.statusNikah,
    stateError: state.app.error,
    statePropinsi: state.master.propinsi,
    stateKotamadya: state.master.kotamadya,
    stateKecamatan: state.master.kecamatan,
    stateKelurahan: state.master.kelurahan,
    stateBahasa: state.app.bahasa,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actGetPendidikan: (action) => dispatch(getPendidikan(action)),
    actSetIdentitas: (body) => dispatch(setIdentitas(body)),
    actGetIdentitas: (nik) => dispatch(getIdentitas(nik)),
    actGetAgama: (action) => dispatch(getAgama(action)),
    actGetPekerjaan: (action) => dispatch(getPekerjaan(action)),
    actGetJenisID: (action) => dispatch(getJenisIdentitas(action)),
    actGetStatusNikah: (action) => dispatch(getStatusNikah(action)),
    actRegister: (body, action) => dispatch(register(body, action)),
    actResetIdentitas: (body, action) => dispatch(resetIdentitas(body, action)),
    actGetPropinsi: (body) => dispatch(getPropinsi(body)),
    actGetKotamadya: (body) => dispatch(getKotamadya(body)),
    actGetKecamatan: (body) => dispatch(getKecamatan(body)),
    actGetKelurahan: (body) => dispatch(getKelurahan(body)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RegisterPage);
